export async function sleep(time) {
  return new Promise(function (resolve, reject) {
    setTimeout(function () {
      resolve()
    }, time)
  })
}

export function body(ctx,body) {
  return (
    ctx.body = body
  )
}
export function error(ctx,error) {
  console.error(error)
  return (
    ctx.body = {
      ok: false,
      message: error.message
    }
  )
}

export function filePath(ctx,uri) {
  let file = ctx.request.files.file

  let fileName,filePath
  if(file.length > 0){
    filePath = []
    for(let f of file){
      fileName = F.getFileName(f.path)
      let fPath = `${domain}/uploads${uri}/${fileName}`
      filePath.push({name:f.name,url:fPath})
    }

    return (
      ctx.body = {
        success: true,
        data:filePath
      }
    )
  }else {
    fileName = F.getFileName(file.path)
    filePath = `${domain}/uploads${uri}/${fileName}`

    return (
      ctx.body = {
        success: true,
        data:{
          name:file.name,
          url:filePath
        }
      }
    )
  }
}



